@font-face {
  font-family: 'Helvetica';
  src: url('../../resources/typo/Helvetica-CE-Medium.otf');
}

@import url('https://fonts.googleapis.com/css2?family=Gayathri:wght@100;400;700&family=Iceberg&display=swap');


.bejuco {
  background-color: #133131;
  font-family: 'Helvetica', sans-serif;
  margin: auto 0;
  width: 100%;
  height: 100vh;


  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: repeat(5, 1fr);
  grid-column-gap: 0px;
  grid-row-gap: 0px;



  // B E J U J  N
  &__bejucote {
    width: 100%;
    grid-area: 1/1/5/3;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
  }


  &__title {
    font-size: 23vh;
    line-height: 150px;
    padding-top: 3rem;
    font-family: 'Iceberg', sans-serif;
    color: #605031;
    text-transform: uppercase;
    margin: 0;

    &--small {
      color: #605031;
      text-transform: uppercase;
      font-family: "Iceberg", sans-serif;
      font-size: 5vh;
      margin: 0;
    }
  }




  &__bejucos {
    width: 100%;
    grid-area: 1 / 3 / 5 / 5;


  }

  &__bejuquitos {
    grid-area: 5 / 1 / 6 / 5;

    &--item {
      color: #957d4f;
      border: #957d4f 2px solid;
      background-color: #133131;
      width: 100%;
      height: 100%;
      display: flex;
      justify-content: center;
      align-items: center;
      font-family: "Iceberg", sans-serif;
      font-size: 40px;
    }
  }

  &__description {
    font-weight: 200;
    font-size: 60px;
    line-height: 50px;
    letter-spacing: -2px;
    margin: 0 3.8rem;
    padding-top: 2rem;
    color: #676767;
    font-family: "Helvetica";
  }

  &__blur {
    position: absolute;
    z-index: 0;

    img {
      filter: blur(3px);
    }
  }

  &__slide {
    position: relative;
    min-height: 80vh;
    display: flex;
    align-items: center;
    margin: 2rem 3.8rem;

    &--content {
      display: grid;
      grid-template-columns: 1fr 1fr 1fr;
      gap: 1rem;
    }

    img {
      z-index: 1;
    }
  }

  .swiper {
    width: 100%;
    height: 100%;
  }

  .swiper-slide {
    text-align: center;
    font-size: 18px;
    background: #fff;

    /* Center slide text vertically */
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .swiper-slide img {
    display: block;
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  body {
    background: #000;
    color: #000;
  }

  .swiper {
    width: 100%;
    height: 300px;
    margin-left: auto;
    margin-right: auto;
  }

  .swiper-slide {
    background-size: cover;
    background-position: center;
  }

  .mySwiper2 {
    height: 100%;
    width: 100%;
  }

  .mySwiper {
    height: 100%;
    box-sizing: border-box;
    padding: 10px 0;
  }

  .mySwiper .swiper-slide {
    width: 25%;
    height: 100%;
    opacity: 0.4;
  }

  .mySwiper .swiper-slide-thumb-active {
    opacity: 1;
  }

  .swiper-slide img {
    display: block;
    width: 100%;
    height: 100%;
    object-fit: cover;
  }




}